import {
  Line,
  Mesh,
  decodePacket2d,
  Packet2d,
  Block,
  BlockReference,
} from "../vendor/protobuf/packet2d";
import { init, decompress, compress } from "@bokuweb/zstd-wasm";
import * as THREE from "three";
import { DbLine } from "../core/DbLine";
import { DbMesh } from "../core/DbMesh";
import { DbLayer } from "../core/DbLayer";
import { DbBlockRecord } from "../core/DbBlockRecord";
import { DbBlockInstance } from "../core/DbBlockInstance";
import { stringUtils } from "../utils/StringUtils";


function loadProtobuf(url: string, callback: (p2d: Packet2d) => void) {
  fetch(url)
    .then((response) => response.arrayBuffer())
    .then((arrayBuffer) => {
      try {
        const decompressed = decompress(new Uint8Array(arrayBuffer));
        const p2d = decodePacket2d(new Uint8Array(decompressed));
        callback(p2d);
      }
      catch (e) {
        onComplete(false);
        console.log(e,'')
      }

    });
}
// let m_lineGeos: { [key: string]:DbLine[] } = {}
// let m_meshGeos: { [key: string]: DbMesh[] } = {}
function onComplete(isComplete:boolean) {
  self.postMessage({ complete: isComplete });
}
function pushData(
  lines: { [key: string]: DbLine[] } | undefined,
  meshs: { [key: string]: DbMesh[] } | undefined,
  instances: { [key: string]: DbBlockInstance[] } | undefined,
  blocks: { [key: string]: DbBlockRecord } | undefined
) {
  self.postMessage({
    lines: lines,
    meshs: meshs,
    instances: instances,
    blocks: blocks,
  });
}
function pushLayers(layers: DbLayer[]) {
  self.postMessage({ layers: layers });
}
/**
 * 
 */
let layerCount: { [key: string]: { keys: string[][]; cal: number[] } } = {};

self.onmessage = ({ data: { command, url } }) => {
  init().then(() => {
    console.time("load");
    loadProtobuf(url, (p2d) => {
      let sum = p2d.count ?? 0;
      let count = 0;
      let layers: DbLayer[] = [];
      p2d.layers?.forEach((layer) => {
        if (layer.id) {
          let name = stringUtils.BytesToString(layer.name ?? new Uint8Array());
          let dbLayer = new DbLayer(name, layer.id);
          dbLayer.Color = layer.color;
          dbLayer.ColorIndex = layer.color_index;
          dbLayer.LineCount = layer.line_count;
          dbLayer.MeshCount = layer.mesh_count;
          layers.push(dbLayer);
          layerCount[layer.id] = {
            cal: [layer.line_count ?? 0, 0, layer.mesh_count ?? 0, 0],
            keys: [[], []],
          };
        }
      });
      pushLayers(layers);
      let urlPre = url.substring(0, url.lastIndexOf("."));
      let lines: { [key: string]: DbLine[] } = {};
      let meshs: { [key: string]: DbMesh[] } = {};
      let blocks: { [key: string]: DbBlockRecord } = {};
      let instacnces: { [key: string]: DbBlockInstance[] } = {};
      let index = 1;
      let path = `${urlPre}_${index}.p2d`;

      LoadBodyData(p2d, lines, meshs, blocks, instacnces);
      if (sum == 0) {
        pushData(lines, undefined, instacnces, blocks);
        pushData(undefined, meshs, instacnces, blocks);
        layerCount = {};
        onComplete(true);
        console.timeEnd("load");
      }
      /**
       *
       * @param p2d 
       */
      const loadproc = (p2d: Packet2d) => {
        count++;
        index++;
        LoadBodyData(p2d, lines, meshs, blocks, instacnces);
        if (sum == count) {
          /**
           * this is a bug
           */
          pushData(lines, undefined, instacnces, blocks);
          pushData(undefined, meshs, instacnces, blocks);
          layerCount = {};
          onComplete(true);
          console.timeEnd("load");
        }
        let path = `${urlPre}_${index}.p2d`;
        if (index <= sum) loadProtobuf(path, loadproc);
      };
      loadProtobuf(path, loadproc);
    });
  });
};

function LoadBodyData(
  p2d: Packet2d,
  lines: { [key: string]: DbLine[] },
  meshs: { [key: string]: DbMesh[] },
  blocks: { [key: string]: DbBlockRecord },
  instacnces: { [key: string]: DbBlockInstance[] }
) {
  p2d.lines?.forEach((line) => {
    addLine(lines, line);
  });
  p2d.meshs?.forEach((mesh) => {
    addMesh(meshs, mesh);
  });
  p2d.block_table?.forEach((blockTable) => {
    addBlocks(blocks, blockTable);
  });
  p2d.blocks?.forEach((block) => {
    addInstance(blocks, instacnces, block);
  });
}

function addLine(lines: { [key: string]: DbLine[] }, line: Line) {
  const layerId = line.layer_id ?? "";

  if (!line.points) return;
  let position: number[] = [];
  line.points.forEach((point) => {
    position.push(point.x ?? 0, point.y ?? 0, point.z ?? 0);
  });
  let key = `${line.layer_id}_${line.color}`;
  // console.log(JSON.stringify(line))
  let linegeo: DbLine = new DbLine({
    layerId: layerId,
    id: line.id ?? "",
    blockId: line.block_id ?? "",
    points: new Float32Array(position),
    indexes: new Int32Array(line.indexes || []),
    root_reference_id: line.root_reference_id,
    block_path: line.block_path,
    root_block_id: line.root_block_id,
  });
  let sphere = new THREE.Sphere();
  let box = new THREE.Box3();
  //
  box.setFromArray(linegeo.points);
  box.getBoundingSphere(sphere);
  linegeo.sphere = sphere;
  linegeo.box = box;
  if (lines[key]) {
    lines[key].push(linegeo);
  } else {
    lines[key] = [linegeo];
    layerCount[layerId].keys[0].push(key);
  }
  /**
   * 
   */
  layerCount[layerId].cal[1]++;
  if (
    layerCount[layerId].cal[1] >= layerCount[layerId].cal[0] &&
    layerCount[layerId].cal[0] != 0
  ) {
    let postLines: { [key: string]: DbLine[] } = {};
    layerCount[layerId].keys[0].forEach((key) => {
      postLines[key] = lines[key];
      delete lines[key];
    });
    layerCount[layerId].keys[0] = [];
    pushData(postLines, undefined, undefined, undefined);
  }
}
function addMesh(meshs: { [key: string]: DbMesh[] }, mesh: Mesh) {
  const layerId = mesh.layer_id ?? "";

  if (!mesh.vertexs) return;
  let key = `${mesh.layer_id}_${mesh.color ?? 0}`;

  let last = mesh.vertexs.length % 9;
  // 
  if (last != 0) {
    mesh.vertexs.splice(mesh.vertexs.length - last, last);
  }
  let mesGeo: DbMesh = new DbMesh({
    layerId: mesh.layer_id ?? "",
    id: mesh.id ?? "",
    blockId: mesh.block_id ?? "",
    vertexs: new Float32Array(mesh.vertexs),
    indexes: new Int32Array(mesh.indexes || []),
    root_reference_id: mesh.root_reference_id,
    block_path: mesh.block_path  ,
  });
  let sphere = new THREE.Sphere();
  let box = new THREE.Box3();
  box.setFromArray(mesGeo.vertexs);
  box.getBoundingSphere(sphere);
  mesGeo.sphere = sphere;
  mesGeo.box = box;
  if (meshs[key]) {
    meshs[key].push(mesGeo);
  } else {
    meshs[key] = [mesGeo];
    layerCount[layerId].keys[1].push(key);
  }

  /**
   * 
   */
  layerCount[layerId].cal[3]++;
  if (
    layerCount[layerId].cal[3] >= layerCount[layerId].cal[2] &&
    layerCount[layerId].cal[0] != 0
  ) {
    let postMesh: { [key: string]: DbMesh[] } = {};
    layerCount[layerId].keys[1].forEach((mesh_key) => {
      postMesh[mesh_key] = meshs[mesh_key];
      delete meshs[mesh_key];
    });
    layerCount[layerId].keys[1] = [];
    pushData(undefined, postMesh, undefined, undefined);
  }
}

function addBlocks(
  blocks: { [key: string]: DbBlockRecord },
  blockTableRecord: Block
) {
  if (blockTableRecord.id) {
    let blockRecord = new DbBlockRecord();
    blockRecord.instances = [];
    blockRecord.Name = stringUtils.BytesToString(
      blockTableRecord.name ?? new Uint8Array()
    );
    blockRecord.Id = blockTableRecord.id;
    blockTableRecord.lines?.forEach((line) => {
      addLine(blockRecord.lines, line);
    });
    blockTableRecord.meshs?.forEach((line) => {
      addMesh(blockRecord.meshs, line);
    });
    blocks[blockTableRecord.id] = blockRecord;
  }
}

function addInstance(
  blocks: { [key: string]: DbBlockRecord },
  instacnces: { [key: string]: DbBlockInstance[] },
  block: BlockReference
) {
  let key = `${block.layer_id}`;
  if (block.block_table_id && block.layer_id && block.matrix && block.id) {
    let ms = block.matrix;
    let matrix = new THREE.Matrix4();
    matrix.set(...(ms as THREE.Matrix4Tuple))
    let blockRecord = blocks[block.block_table_id];
    let instance: DbBlockInstance = new DbBlockInstance({
      id: block.id,
      blockId: block.block_id ?? "",
      layerId: block.layer_id,
      blockRecord: blockRecord,
      matrix: matrix,
    });
    if (!instacnces[key]) instacnces[key] = [];
    instacnces[key].push(instance);
    blockRecord.instances.push(instance);
  }
}
