export interface Packet2d {
  index?: number;
  count?: number;
  layers?: Layer[];
  blocks?: BlockReference[];
  block_table?: Block[];
  lines?: Line[];
  meshs?: Mesh[];
  line_types?: LineType[];
}

export function encodePacket2d(message: Packet2d): Uint8Array {
  let bb = popByteBuffer();
  _encodePacket2d(message, bb);
  return toUint8Array(bb);
}

function _encodePacket2d(message: Packet2d, bb: ByteBuffer): void {
  // optional uint32 index = 1;
  let $index = message.index;
  if ($index !== undefined) {
    writeVarint32(bb, 8);
    writeVarint32(bb, $index);
  }

  // optional uint32 count = 2;
  let $count = message.count;
  if ($count !== undefined) {
    writeVarint32(bb, 16);
    writeVarint32(bb, $count);
  }

  // repeated Layer layers = 3;
  let array$layers = message.layers;
  if (array$layers !== undefined) {
    for (let value of array$layers) {
      writeVarint32(bb, 26);
      let nested = popByteBuffer();
      _encodeLayer(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated BlockReference blocks = 4;
  let array$blocks = message.blocks;
  if (array$blocks !== undefined) {
    for (let value of array$blocks) {
      writeVarint32(bb, 34);
      let nested = popByteBuffer();
      _encodeBlockReference(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated Block block_table = 5;
  let array$block_table = message.block_table;
  if (array$block_table !== undefined) {
    for (let value of array$block_table) {
      writeVarint32(bb, 42);
      let nested = popByteBuffer();
      _encodeBlock(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated Line lines = 6;
  let array$lines = message.lines;
  if (array$lines !== undefined) {
    for (let value of array$lines) {
      writeVarint32(bb, 50);
      let nested = popByteBuffer();
      _encodeLine(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated Mesh meshs = 7;
  let array$meshs = message.meshs;
  if (array$meshs !== undefined) {
    for (let value of array$meshs) {
      writeVarint32(bb, 58);
      let nested = popByteBuffer();
      _encodeMesh(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated LineType line_types = 8;
  let array$line_types = message.line_types;
  if (array$line_types !== undefined) {
    for (let value of array$line_types) {
      writeVarint32(bb, 66);
      let nested = popByteBuffer();
      _encodeLineType(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }
}

export function decodePacket2d(binary: Uint8Array): Packet2d {
  return _decodePacket2d(wrapByteBuffer(binary));
}

function _decodePacket2d(bb: ByteBuffer): Packet2d {
  let message: Packet2d = {} as any;

  end_of_message: while (!isAtEnd(bb)) {
    let tag = readVarint32(bb);

    switch (tag >>> 3) {
      case 0:
        break end_of_message;

      // optional uint32 index = 1;
      case 1: {
        message.index = readVarint32(bb) >>> 0;
        break;
      }

      // optional uint32 count = 2;
      case 2: {
        message.count = readVarint32(bb) >>> 0;
        break;
      }

      // repeated Layer layers = 3;
      case 3: {
        let limit = pushTemporaryLength(bb);
        let values = message.layers || (message.layers = []);
        values.push(_decodeLayer(bb));
        bb.limit = limit;
        break;
      }

      // repeated BlockReference blocks = 4;
      case 4: {
        let limit = pushTemporaryLength(bb);
        let values = message.blocks || (message.blocks = []);
        values.push(_decodeBlockReference(bb));
        bb.limit = limit;
        break;
      }

      // repeated Block block_table = 5;
      case 5: {
        let limit = pushTemporaryLength(bb);
        let values = message.block_table || (message.block_table = []);
        values.push(_decodeBlock(bb));
        bb.limit = limit;
        break;
      }

      // repeated Line lines = 6;
      case 6: {
        let limit = pushTemporaryLength(bb);
        let values = message.lines || (message.lines = []);
        values.push(_decodeLine(bb));
        bb.limit = limit;
        break;
      }

      // repeated Mesh meshs = 7;
      case 7: {
        let limit = pushTemporaryLength(bb);
        let values = message.meshs || (message.meshs = []);
        values.push(_decodeMesh(bb));
        bb.limit = limit;
        break;
      }

      // repeated LineType line_types = 8;
      case 8: {
        let limit = pushTemporaryLength(bb);
        let values = message.line_types || (message.line_types = []);
        values.push(_decodeLineType(bb));
        bb.limit = limit;
        break;
      }

      default:
        skipUnknownField(bb, tag & 7);
    }
  }

  return message;
}

export interface Block {
  id?: string;
  name?: Uint8Array;
  lines?: Line[];
  meshs?: Mesh[];
  blocks?: BlockReference[];
  point_count?: Long;
}

export function encodeBlock(message: Block): Uint8Array {
  let bb = popByteBuffer();
  _encodeBlock(message, bb);
  return toUint8Array(bb);
}

function _encodeBlock(message: Block, bb: ByteBuffer): void {
  // optional string id = 1;
  let $id = message.id;
  if ($id !== undefined) {
    writeVarint32(bb, 10);
    writeString(bb, $id);
  }

  // optional bytes name = 2;
  let $name = message.name;
  if ($name !== undefined) {
    writeVarint32(bb, 18);
    writeVarint32(bb, $name.length), writeBytes(bb, $name);
  }

  // repeated Line lines = 3;
  let array$lines = message.lines;
  if (array$lines !== undefined) {
    for (let value of array$lines) {
      writeVarint32(bb, 26);
      let nested = popByteBuffer();
      _encodeLine(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated Mesh meshs = 4;
  let array$meshs = message.meshs;
  if (array$meshs !== undefined) {
    for (let value of array$meshs) {
      writeVarint32(bb, 34);
      let nested = popByteBuffer();
      _encodeMesh(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // repeated BlockReference blocks = 5;
  let array$blocks = message.blocks;
  if (array$blocks !== undefined) {
    for (let value of array$blocks) {
      writeVarint32(bb, 42);
      let nested = popByteBuffer();
      _encodeBlockReference(value, nested);
      writeVarint32(bb, nested.limit);
      writeByteBuffer(bb, nested);
      pushByteBuffer(nested);
    }
  }

  // optional uint64 point_count = 6;
  let $point_count = message.point_count;
  if ($point_count !== undefined) {
    writeVarint32(bb, 48);
    writeVarint64(bb, $point_count);
  }
}

export function decodeBlock(binary: Uint8Array): Block {
  return _decodeBlock(wrapByteBuffer(binary));
}

function _decodeBlock(bb: ByteBuffer): Block {
  let message: Block = {} as any;

  end_of_message: while (!isAtEnd(bb)) {
    let tag = readVarint32(bb);

    switch (tag >>> 3) {
      case 0:
        break end_of_message;

      // optional string id = 1;
      case 1: {
        message.id = readString(bb, readVarint32(bb));
        break;
      }

      // optional bytes name = 2;
      case 2: {
        message.name = readBytes(bb, readVarint32(bb));
        break;
      }

      // repeated Line lines = 3;
      case 3: {
        let limit = pushTemporaryLength(bb);
        let values = message.lines || (message.lines = []);
        values.push(_decodeLine(bb));
        bb.limit = limit;
        break;
      }

      // repeated Mesh meshs = 4;
      case 4: {
        let limit = pushTemporaryLength(bb);
        let values = message.meshs || (message.meshs = []);
        values.push(_decodeMesh(bb));
        bb.limit = limit;
        break;
      }

      // repeated BlockReference blocks = 5;
      case 5: {
        let limit = pushTemporaryLength(bb);
        let values = message.blocks || (message.blocks = []);
        values.push(_decodeBlockReference(bb));
        bb.limit = limit;
        break;
      }

      // optional uint64 point_count = 6;
      case 6: {
        message.point_count = readVarint64(bb, /* unsigned */ true);
        break;
      }

      default:
        skipUnknownField(bb, tag & 7);
    }
  }

  return message;
}

export interface BlockReference {
  id?: string;
  layer_id?: string;
  matrix?: number[];
  block_id?: string;
  block_table_id?: string;
  color?: number;
  color_index?: number;
  raw_layer_id?: string;
}

export function encodeBlockReference(message: BlockReference): Uint8Array {
  let bb = popByteBuffer();
  _encodeBlockReference(message, bb);
  return toUint8Array(bb);
}

function _encodeBlockReference(message: BlockReference, bb: ByteBuffer): void {
  // optional string id = 1;
  let $id = message.id;
  if ($id !== undefined) {
    writeVarint32(bb, 10);
    writeString(bb, $id);
  }

  // optional string layer_id = 2;
  let $layer_id = message.layer_id;
  if ($layer_id !== undefined) {
    writeVarint32(bb, 18);
    writeString(bb, $layer_id);
  }

  // repeated float matirx = 3;
  let array$matirx = message.matrix;
  if (array$matirx !== undefined) {
    let packed = popByteBuffer();
    for (let value of array$matirx) {
      writeFloat(packed, value);
    }
    writeVarint32(bb, 26);
    writeVarint32(bb, packed.offset);
    writeByteBuffer(bb, packed);
    pushByteBuffer(packed);
  }

  // optional string block_id = 4;
  let $block_id = message.block_id;
  if ($block_id !== undefined) {
    writeVarint32(bb, 34);
    writeString(bb, $block_id);
  }

  // optional string block_table_id = 5;
  let $block_table_id = message.block_table_id;
  if ($block_table_id !== undefined) {
    writeVarint32(bb, 42);
    writeString(bb, $block_table_id);
  }

  // optional uint32 color = 6;
  let $color = message.color;
  if ($color !== undefined) {
    writeVarint32(bb, 48);
    writeVarint32(bb, $color);
  }

  // optional uint32 color_index = 7;
  let $color_index = message.color_index;
  if ($color_index !== undefined) {
    writeVarint32(bb, 56);
    writeVarint32(bb, $color_index);
  }

  // optional string raw_layer_id = 8;
  let $raw_layer_id = message.raw_layer_id;
  if ($raw_layer_id !== undefined) {
    writeVarint32(bb, 66);
    writeString(bb, $raw_layer_id);
  }
}

export function decodeBlockReference(binary: Uint8Array): BlockReference {
  return _decodeBlockReference(wrapByteBuffer(binary));
}

function _decodeBlockReference(bb: ByteBuffer): BlockReference {
  let message: BlockReference = {} as any;

  end_of_message: while (!isAtEnd(bb)) {
    let tag = readVarint32(bb);

    switch (tag >>> 3) {
      case 0:
        break end_of_message;

      // optional string id = 1;
      case 1: {
        message.id = readString(bb, readVarint32(bb));
        break;
      }

      // optional string layer_id = 2;
      case 2: {
        message.layer_id = readString(bb, readVarint32(bb));
        break;
      }

      // repeated float matirx = 3;
      case 3: {
        let values = message.matrix || (message.matrix = []);
        if ((tag & 7) === 2) {
          let outerLimit = pushTemporaryLength(bb);
          while (!isAtEnd(bb)) {
            values.push(readFloat(bb));
          }
          bb.limit = outerLimit;
        } else {
          values.push(readFloat(bb));
        }
        break;
      }

      // optional string block_id = 4;
      case 4: {
        message.block_id = readString(bb, readVarint32(bb));
        break;
      }

      // optional string block_table_id = 5;
      case 5: {
        message.block_table_id = readString(bb, readVarint32(bb));
        break;
      }

      // optional uint32 color = 6;
      case 6: {
        message.color = readVarint32(bb) >>> 0;
        break;
      }

      // optional uint32 color_index = 7;
      case 7: {
        message.color_index = readVarint32(bb) >>> 0;
        break;
      }

      // optional string raw_layer_id = 8;
      case 8: {
        message.raw_layer_id = readString(bb, readVarint32(bb));
        break;
      }

      default:
        skipUnknownField(bb, tag & 7);
    }
  }

  return message;
}

