import * as THREE from "three";
import { DbBlockInstance } from "./DbBlockInstance";
import { DbLine } from "./DbLine";
import { DbMesh } from "./DbMesh";

/**
 * modelinstance
 */
export class DbBlockRecord{
    private  m_lineGeos:{[key:string]:DbLine[]} = {}
    private  m_meshGeos:{[key:string]:DbMesh[]} = {}
    instances:DbBlockInstance[] = [];
    Name: string|undefined;
    Id: string|undefined;
    public  get meshs(){
        return this.m_meshGeos;
    }
    public  get lines(){
        return this.m_lineGeos;
    }
}